import { amber, deepOrange, grey } from '@mui/material/colors';

const getDesignTokens = (mode) => ({
    palette: {
      mode,
      primary: {
        ...amber,
        ...(mode === 'dark' && {
          main: '#62239a',
        }),
      },
      ...(mode === 'dark' && {
        background: {
          default: deepOrange[900],
          paper: deepOrange[900],
        },
      }),
      text: {
        ...(mode === 'light'
          ? {
              primary: grey[900],
              secondary: grey[800],
            }
          : {
              primary: '#240b03',
              secondary: grey[500],
            }),
      },
    },
  });

  export default getDesignTokens